.event-card {
    width: 200px;
    margin: 10px;
    padding: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    text-align: center;
  }
  
  .event-card img {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 5px;
  }
  
  .event-card h3 {
    margin: 10px 0 5px;
    font-size: 18px;
  }
  
  .event-card p {
    margin: 0;
    color: #777;
  }
  